.navegacao{
    
    display:grid;
    grid-template-columns: 30% 70%;
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 1%;
    color: rgb(246, 246, 252);
   
}

.logo{
    border-radius: 50%;
    height: 60px;
    width: 60PX;
    object-fit: cover;
    padding: 5PX;
}

.d-inline{
display: none;
}

.navbar{
    display:grid;
    grid-template-columns: 20% 20% 20% 20%;
    list-style: none;
    align-items: center;
    justify-content: center;
    justify-items: center;
    color: rgb(162, 162, 184);
    
}

li, a{
    color: rgb(168, 168, 179);
width: auto;
margin-right: -60%;
min-width: 80%;
text-align: center;
justify-content: center;
justify-items: center;
font-size: large;

}

li:hover{
    color: rgb(220, 228, 230);

}
a:hover{
    color: rgb(220, 228, 230);
   font-size: 20px;
   text-decoration:underline;
}

a{
    text-decoration: none;
}

a:visited, li:visited{
    display: none;
}



@media only screen and (max-width:480px){
    .navegacao{
    display: flex;
    flex-direction: row-reverse;
    width: 98%;
    height: auto;
    align-items: center;
    justify-content: space-between;
  
  margin-bottom: 70px;


    }

    
    
}




@media only screen and (max-width:480px){
    .navbar{
   
  
    display: none;
    }
    
}


@media only screen and (max-width:480px){
.d-inline{
   
    display: flex;
    width: 35px;
    height: auto;
    flex-direction: row-reverse;
     margin-left: 15px;
    align-items: center;
   
    justify-content: space-between;
    }

    .menu{
        height: auto;
        width:60px;
        height: 150px;
        display: flex;
        flex-direction: column;     



    }

    .menu-disable{
        display: none;   



    }

    button{
        background: rgb(18, 18, 20);
        border:  none;
        color: rgb(220, 228, 230);
        font-size: x-large;
    }
   .dropdown-menu{

align-items: left;
text-align: left;
width: 70px;
height: 100px;
margin-bottom: 10px;
   }
    .dropdown-item{
margin-top: 3px;
align-items: center;
justify-content: center;
width: 100%;
border-radius: 5px;
text-align: left;
margin-left: -5px;
padding: 1px;
    }
}


#dropdown-autoclose-false{
    color: rgb(238, 232, 232);
    border:  none;
font-size: x-large;
justify-content: center;
background: rgb(18, 18, 20);
}


