#sobre{
    display: flex;
    color: rgb(252, 249, 249);
    height: auto;
   align-items: center;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    flex-direction: column;
    background-color: rgb(32, 32, 36);
   width: 98%;
    margin: 0 auto;
   margin-top: 6px;
   padding-bottom: 20px;
}


.article{
   padding: 5px 10px 15px 10px;
   display: flex;
    color: rgb(137, 138, 139);
   text-align: left;
    margin: 0 auto;
    width: 40%;
    height: auto;
    background-color: rgb(44, 44, 46);
    flex-wrap: wrap;
    font-family:"Times New Roman", Times, serif;
   align-items: center;
   justify-content: space-between;

   
   
}






@media only screen and (max-width:480px){
    #sobre{
        width: 98%;
        height: auto;
        justify-content: center;
        align-items: center;
        text-align: center;
       padding-top: 10px;
       padding-bottom:10px;
    }
    
    .article {
      height: auto;
      width: 85%;
    
    }
    .titulo{
   margin-bottom: 5px;
      
      }
}

