.tecnologias{
    width: 100%;
    height: 700px;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
   flex-wrap: wrap;
    gap: 5px;
}

.tec{
    
    display: flex;
    flex-direction: column;
    gap: 10px;
    color: rgb(242, 245, 247);
    align-items: center;
    margin-bottom: 5px;
}

.estudando{
    
    display: flex;
    flex-direction: row;
    gap: 10px;
    color: rgb(242, 245, 247);
    align-items: center;
    margin-bottom: 5px;
    margin: 0 auto;
    width: 40%;
    height: 250px;
    display: flex;
    flex-direction: column;
    background-color: rgb(44, 44, 46);
    flex-wrap: wrap;
   
   justify-content: space-around;
   align-items: center;
}


.Conhecimentos{
    margin: 0 auto;
    width: 40%;
    height: 250px;
    display: flex;
    flex-direction: column;
    background-color: rgb(44, 44, 46);
    flex-wrap: wrap;
   
   justify-content: space-around;
   align-items: center;
}


@media only screen and (max-width:480px){
    .tecnologias{
    height: auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: center;
    text-align: center;

    padding-bottom: 10px;
    
     }
     
.Conhecimentos{
    height: 200px;
