section{

    width: 100px;
    height: 100px;
    background-color:white;
    text-align: center;
    border-radius: 50%;
    transform: translateY(0px);
    
    
    }
    
    
    
    .img{
        width:50px;
        height: 50px;
        margin-top: 25%;
       border-radius: 1%;
       color: rgb(248, 248, 248) ;
       
    }
    
    .img:hover{
        width:50px;
        height: 50px;
        margin-top: 20%;
       border-radius: 1%;
     
       
    }
    




 section:hover{
    border-radius: 10px;
    width: 100px;
    height: 100px;

}



@keyframes animate{
    0% { 
    transform: translate(700px);
    }
    
    50% { transform: translate(2px);
    }
    100% { transform: translate(0px);
    }
    }

    section{
        content:'' ;
       
       animation: animate 5s linear ;
        position: relative;
      
    }


