
#left{
    width: 100%;
    margin: 0 auto;
    justify-content: center;
    text-align: center;
    align-items: center;
    
}



.Conteudoleft{
    height: auto;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: flex-start;
    justify-content: center;
    text-align: center;
    align-items: center;
    width: 30%;
   padding-top: 5%;
   padding-bottom: 5%;
   margin-top: 15px;
  
    }

    


#left  h2{
   
    font-size: 35px;
    margin-top: 10px;
    margin-bottom: 1px;
    color: rgb(252, 249, 252);
}

#left  h5{
    width: 250px;
    font-size: 20px;
    margin-top: 0px;
    color: rgb(248, 248, 248);
    margin-bottom: 0px;
}



#left img{
   width: 250px;
   height: 250px;
   object-fit: cover;
   border-radius: 50%;
  
  

}

.perfil{
    display: flex;
    width: 252px;
   height: 252px;
   justify-content: center;
   align-items: center;
   border-radius: 50%;
  
}



@keyframes animatep{
    0% { 
    transform: translate(-300px);
    }
    
    50% { transform: translate(-10px);
    }
    100% { transform: translate(0px);
    }
    }

   
        



    @media only screen and (max-width:480px){
        .Conteudoleft {
        width: 100%;
        margin:  0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
        }
        
    }
  
    
    @media only screen and (max-width:480px){
        #left img {
        
           width: 150px;
           height: 150px;
        
        }
        
    }