#main{
  
    margin-top: 5%;
  align-items: center;
 justify-content: center;
    display:flex;
    flex-wrap: wrap;
    flex-direction: row;
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 5%;
   height: auto;

row-gap: 6px;
}

#left{
    background-color: rgb(32, 32, 36);
display: grid;
grid-template-columns: 100%;
align-items: center;
justify-content: center;
margin: 0 auto;
margin-bottom:15px;
padding-bottom: 20px;

}




#Right{
   
 
   display: flex;
   background-color: rgb(32, 32, 36);
   padding-top: 50px;
   align-items: center;
   justify-content: center;
   justify-items: center;
   gap: 1px;
   
 
}






@media only screen and (max-width:480px){
    #main {
 
   margin-top: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
   flex-wrap: wrap;
 padding-bottom: 5px;
  
    
    }
    
}


@media only screen and (max-width:480px){
    #Right {
        align-items: center;
        justify-content: center;
        justify-items: center;
      display: flex;




 
    }
    
}

@media only screen and (max-width:480px){
    #left {    
 display: flex;
flex-direction: column;
 margin-top: 3%;
 padding-top: 0;

 width: 360px;
    }
    
}


