#footer{

   height:300px;
   width: 98%;
   margin: 0 auto;
  margin-top: 6px;
  background-color: rgb(32, 32, 36);
   display: flex;
   flex-direction: column; 
   margin-left: auto;
   margin-right: auto;
   text-align: center;
   flex-wrap: wrap;
   justify-items: center;
  padding-bottom: 25px;

}

.redes{
    display: flex;
    flex-direction: row; 
    background-color: rgb(44, 44, 46);
    width: 40%;

    align-self: center;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: 0 auto;
    height: 150px;
    
    padding-left: 10px;
    padding-right:10px;

   padding-top: 15px;
  margin-bottom: 5px;
  margin-top: 5px;

}

.Linkedin{
    width: 60px;
    height: 60px;
    margin: 0 auto;
    display: flex;
  

}

.imgcontato{
   background-color: rgb(92, 86, 86);
   border: 1px solid rgb(92, 86, 86);
    width: 50px;
    height: 50px;
    text-align: center;
    justify-content: center;
    justify-items: center;
    border-radius: 50px;
   
   
    
}


.Github{
   
    width: 60px;
    height: 60px;
    
    margin: 0 auto;
   display: flex;
 
  
}

.Email{
   
    width: 60px;
    height: 60px;
   margin: 0 auto;
   display: flex;
   
}

@media only screen and (max-width:480px){
    #footer{
      
        width: 98%;
          height: auto;
          justify-content: center;
          align-items: center;
          text-align: center;
        

 
        }
        
       .redes{
        width: 85%;
       } 


    }